import axios from "axios";

const backendPortNumber = "5001";
const serverUrl =
  "http://" + window.location.hostname + ":" + backendPortNumber + "/";

async function get(endpoint, params = "") {
  try {
    console.log(
      `%cGET 요청 ${serverUrl + endpoint + "/" + params}`,
      "color: #a25cd1;"
    );
  }
  catch (err) {
    console.log('GET 요청에 실패했습니다\n',err);
  }
  return axios.get(serverUrl + endpoint + "/" + params, {
    // JWT 토큰을 헤더에 담아 백엔드 서버에 보냄.
    headers: {
      Authorization: `Bearer ${sessionStorage.getItem("userToken")}`,
    },
  });

}

async function post(endpoint, data) {
  const bodyData = JSON.stringify(data);
  try{
    // JSON.stringify 함수: Javascript 객체를 JSON 형태로 변환함.
    // 예시: {name: "Kim"} => {"name": "Kim"}
    console.log(`%cPOST 요청: ${serverUrl + endpoint}`, "color: #296aba;");
    console.log(`%cPOST 요청 데이터: ${bodyData}`, "color: #296aba;");
  }
  catch (err) {
    console.log('POST 요청에 실패했습니다\n',err);
  }

  return axios.post(serverUrl + endpoint, bodyData, {
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${sessionStorage.getItem("userToken")}`,
    },
  });
}

async function postImg(endpoint, data) {
  const bodyData = JSON.stringify(data);
  try{
    // JSON.stringify 함수: Javascript 객체를 JSON 형태로 변환함.
    // 예시: {name: "Kim"} => {"name": "Kim"}
    console.log(`%c이미지 POST 요청: ${serverUrl + endpoint}`, "color: #296aba;");
    console.log(`%cPOST 요청 데이터: ${bodyData}`, "color: #296aba;");
  }
  catch (err) {
    console.log('POST 요청에 실패했습니다\n',err);
  }

  return axios.post(serverUrl + endpoint, bodyData, {
    headers: {
      "Content-Type": 'multipart/form-data',
      Authorization: `Bearer ${sessionStorage.getItem("userToken")}`,
    },
  });
}

async function put(endpoint, data) {
  const bodyData = JSON.stringify(data);
  try{
  // JSON.stringify 함수: Javascript 객체를 JSON 형태로 변환함.
  // 예시: {name: "Kim"} => {"name": "Kim"}
  console.log(`%cPUT 요청: ${serverUrl + endpoint}`, "color: #059c4b;");
  console.log(`%cPUT 요청 데이터: ${bodyData}`, "color: #059c4b;");
  }
  catch(err) {
    console.log('PUT 요청에 실패했습니다\n',err);
  }

  return axios.put(serverUrl + endpoint, bodyData, {
    headers: {
      "Content-Type": "application/json",
      Authorization: `Bearer ${sessionStorage.getItem("userToken")}`,
    },
  });
}

// 아래 함수명에 관해, delete 단어는 자바스크립트의 reserved 단어이기에,
// 여기서는 우선 delete 대신 del로 쓰고 아래 export 시에 delete로 alias 함.
async function del(endpoint, params = "") {
  try{
    console.log(`DELETE 요청 ${serverUrl + endpoint + "/" + params}`);
  }
  catch(err) {
    console.log('DELETE 요청에 실패했습니다\n',err);
  }

  return axios.delete(serverUrl + endpoint + "/" + params, {
    headers: {
      Authorization: `Bearer ${sessionStorage.getItem("userToken")}`,
    },
  });
}

async function imgpost(endpoint, img) {
  const formData = new FormData();
  formData.append('image', img );

  try{
    console.log(`%cPOST 요청: ${serverUrl + endpoint}`, "color: #059c4b;");
    console.log(`%cPOST 요청 데이터: ${formData}`, "color: #059c4b;");
  }
  catch(err) {
    console.log('POST 요청에 실패했습니다\n', err);
  }

  return axios.post(serverUrl + endpoint, formData, {
    headers: {
      "Content-Type": 'multipart/form-data',
    },
  });
}


// 아래처럼 export한 후, import * as A 방식으로 가져오면,
// A.get, A.post 로 쓸 수 있음.
export { get, post, postImg, put, del as delete, imgpost };
